<template>
  <div>
    <h1>Profile</h1>
    <div>Nom: {{ name }}</div>
    <div>Courriel: {{ email }}</div>
  </div>
</template>

<script>
export default {
  async created () {
    await this.$store.dispatch('profile/getProfile')
    if (this.onError) {
      // TODO: rediriger vers une page d'erreur...
      console.log("TODO: rediriger vers une page d'erreur")
    }
  },
  computed: {
    name () {
      return this.$store.state.profile.name
    },
    email () {
      return this.$store.state.profile.email
    },
    onError () {
      return this.$store.state.profile.onError
    }
  }
}
</script>
