import { API } from '@/shared/config'
import { parseAxiosErrorToAppError } from '@/shared/errorHelper'
import axios from 'axios'

async function getAllTrailById (id) {
  try {
    const response = await axios.get(`${API}/api/parks/${id}/trails`)
    return response.data
  } catch (error) {
    throw parseAxiosErrorToAppError(error)
  }
}

async function getSegmentsFromTrailId (id) {
  try {
    const response = await axios.get(`${API}/api/segments/${id}`)
    return response.data
  } catch (error) {
    throw parseAxiosErrorToAppError(error)
  }
}

export const trailService = {
  getAllTrailById,
  getSegmentsFromTrailId
}
