import { trailService } from '../../services/trailService'

const state = {
  trails: [],
  segmentsCoordinates: [],
  segments: [],
  fetchError: '',
  onError: false
}

const getters = {
  getTrails (state) {
    return state.trails
  },
  getTrailById (state, id) {
    return state.trails.find(trail => trail.id === id)
  },
  getErrorMessage (state) {
    return state.fetchError
  },
  getCoordinatesArray (state) {
    return state.segmentsCoordinates
  },
  getFirstCoordinates (state) {
    return state.segmentsCoordinates[0][0]
  }
}

const mutations = {
  initializeTrails (state, trails) {
    state.trails = trails
  },
  initializeFetchError (state, message) {
    state.fetchError = message
  },
  setOnError (state) {
    state.onError = true
  },
  sortTrailsByName (state) {
    // https://stackoverflow.com/questions/6712034/sort-array-by-firstname-alphabetically-in-javascript
    state.trails.sort((a, b) => a.name.localeCompare(b.name))
  },
  addCoordinatesToArray (state, coordinates) {
    state.segmentsCoordinates.push(coordinates)
  },
  addSegmentToArray (state, segment) {
    state.segments.push(segment)
  },
  clearCoordinatesArray (state) {
    state.segmentsCoordinates = []
  },
  clearSegmentsArray (state) {
    state.segments = []
  }
}

const actions = {
  async fetchTrailsById ({ commit }, id) {
    try {
      const trails = await trailService.getAllTrailById(id)
      commit('initializeTrails', trails)
      commit('sortTrailsByName')
    } catch (error) {
      commit('initializeFetchError', error.message)
      commit('setOnError')
    }
  },
  async fetchSegmentFromTrails ({ commit }, array) {
    try {
      commit('clearCoordinatesArray')
      commit('clearSegmentsArray')
      let segmentId
      for (let i = 0; i < array.length; i++) {
        segmentId = array[i]
        const segment = await trailService.getSegmentsFromTrailId(segmentId)
        commit('addCoordinatesToArray', segment.coordinates)
        commit('addSegmentToArray', segment)
      }
    } catch (error) {
      commit('initializeFetchError', error.message)
      commit('setOnError')
    }
  }
}

export default {
  namespaced: true,
  state,
  actions,
  mutations,
  getters
}
