import { likeService } from '../../services/likeService'

const state = {
  likedTrails: [],
  trailAllLikes: [],
  fetchError: '',
  onError: false
}

const getters = {
  getLiked (state) {
    return state.likedTrails
  },
  getAllTrailLikes (state) {
    return state.trailAllLikes
  },
  getErrorMessage (state) {
    return state.fetchError
  },
  getLikeId: state => id => {
    return state.likedTrails.find(trail => trail.trailId === id)
  }
}

const mutations = {
  initializeLikedTrails (state, trails) {
    state.likedTrails = trails
  },
  initializeTrailAllLikes (state, trailsLikes) {
    state.trailAllLikes = trailsLikes
  },
  initializeFetchError (state, message) {
    state.fetchError = message
  },
  setOnError (state) {
    state.onError = true
  }
}

const actions = {
  async fetchLikedTrails ({ commit }, payload) {
    try {
      const likedTrails = await likeService.getUsersLike(payload.userId)
      commit('initializeLikedTrails', likedTrails)
    } catch (error) {
      commit('initializeFetchError', error.message)
      commit('setOnError')
    }
  },
  async fetchAllLikesFromTrailsId ({ commit }, id) {
    try {
      const likedTrails = await likeService.fetchAllLikesFromTrailsId(id)
      commit('initializeTrailAllLikes', likedTrails)
    } catch (error) {
      commit('initializeFetchError', error.message)
      commit('setOnError')
    }
  },
  async addLike ({ commit }, payload) {
    try {
      await likeService.addLikeService(payload.trailId, payload.userId)
    } catch (error) {
      commit('initializeFetchError', error.message)
      commit('setOnError')
    }
  },
  async removeLike ({ commit }, userId) {
    try {
      await likeService.removeLike(userId)
    } catch (error) {
      commit('initializeFetchError', error.message)
      commit('setOnError')
    }
  }
}

export default {
  namespaced: true,
  state,
  actions,
  mutations,
  getters
}
