<template>
  <div class="d-flex justify-content-center">
    <l-map style="height: 350px" :zoom="zoom" :center="center" class="w-75">
      <l-tile-layer :url="url" :attribution="attribution"></l-tile-layer>
      <div v-for="segment in segments" v-bind:key="segment.id">
        <l-polyline
          :lat-lngs="segment.coordinates"
          :color="determineColor(segment.level)"
        ></l-polyline>
      </div>
    </l-map>
  </div>
</template>

<script>
import { LMap, LTileLayer, LPolyline } from 'vue2-leaflet'

export default {
  components: {
    LMap,
    LTileLayer,
    LPolyline
  },
  created: function () {
    this.$parent.$on('drawMap', this.drawOnMap)
  },
  data () {
    return {
      url: 'https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png',
      attribution:
        '&copy; <a target="_blank" href="http://osm.org/copyright">OpenStreetMap</a> contributors',
      zoom: 15,
      center: this.center,
      polyline: {
        latlngs: this.$store.state.trails.segmentsCoordinates,
        color: 'black'
      }
    }
  },
  methods: {
    async drawOnMap (coordinates) {
      this.center = coordinates[0][0]
    },
    determineColor (difficulty) {
      switch (difficulty) {
        case 'Très Difficile':
          return 'red'
        case 'Difficile':
          return 'orange'
        case 'Intermédiaire':
          return 'blue'
        case 'Facile':
          return 'green'
        case 'Inconnue':
          return 'black'
      }
    }
  },
  computed: {
    segments () {
      return this.$store.state.trails.segments
    },
    segmentsDifficulties () {
      return this.$store.state.trails.segmentsDifficulties
    }
  }
}
</script>
