import { parkService } from '../../services/parkService'

const state = {
  parks: [],
  fetchError: '',
  onError: false
}

const getters = {
  getParks (state) {
    return state.parks
  },
  getErrorMessage (state) {
    return state.fetchError
  }
}

const mutations = {
  initializeParks (state, parks) {
    state.parks = parks
  },
  initializeFetchError (state, message) {
    state.fetchError = message
  },
  setOnError (state) {
    state.onError = true
  },
  sortParksByName (state) {
    // https://stackoverflow.com/questions/6712034/sort-array-by-firstname-alphabetically-in-javascript
    state.parks.sort((a, b) => a.name.localeCompare(b.name))
  }
}

const actions = {
  async fetchParks ({ commit }) {
    try {
      const parks = await parkService.getAllParks()
      commit('initializeParks', parks)
      commit('sortParksByName')
    } catch (error) {
      commit('initializeFetchError', error.message)
      commit('setOnError')
    }
  }
}

export default {
  namespaced: true,
  state,
  actions,
  mutations,
  getters
}
