<template>
  <div class="container">
    <h1 class="text-center mt-xl">
      Bienvenue sur le site de regroupement de sentiers de la SÉPAQ
    </h1>
    <br />
    <div class="row">
      <div class="home text-center col-6">
        <p>Veuillez choisir un des parcs :</p>
        <p v-if="hasError == true">{{ errorMessage }}</p>
        <select
          v-if="hasError == false"
          v-model="parkSelected"
          @change="getTrailById(parkSelected.parkId)"
        >
          <option
            v-for="park in parks"
            v-bind:key="park.id"
            :value="{ parkId: park.id }"
            >{{ park.name }}
          </option>
        </select>
        <div v-if="parkSelected != ''">
          <p>Ensuite veuillez choisir un sentier :</p>
          <p v-if="hasError == true">{{ errorMessage }}</p>
          <select
            v-if="hasError == false"
            v-model="trailSelected"
            @change="
              displayOnMap(trailSelected.segmentsId, trailSelected.trailId)
            "
          >
            <option
              v-for="trail in trailsById"
              v-bind:key="trail.id"
              :value="{
                trailId: trail.id,
                segmentsId: trail.segments,
                trailLevel: trail.difficulties
              }"
              >{{ trail.name }}
            </option>
          </select>
        </div>
      </div>
      <div class="col-6" v-if="trailSelected != ''">
        <Map />

        <img
          @click="likeMethod(trailSelected.trailId)"
          id="emptyHeart"
          src="@/assets/emptyHeart.png"
          alt="EmptyHeart"
          v-if="isLiked === false"
        />
        <img
          v-if="isLiked === true"
          id="filledHeart"
          src="@/assets/filledHeart.png"
          alt="filledHeart"
          @click="likeMethod(trailSelected.trailId)"
        />
        <p>{{ allTrailLikes }}</p>
      </div>
    </div>
  </div>
</template>

<script>
import Map from '@/components/Map.vue'
export default {
  components: {
    Map
  },
  async created () {
    await this.getParks()
    if (this.$store.getters['authentication/isLoggedIn']) {
      await this.$store.dispatch('like/fetchLikedTrails', {
        userId: this.$store.getters['authentication/getTokenUserId']
      })
    }
  },
  data () {
    return {
      hasError: false,
      parkSelected: '',
      trailSelected: '',
      parkId: 3,
      isLiked: false
    }
  },
  methods: {
    async getParks () {
      try {
        await this.$store.dispatch('park/fetchParks', null, { root: true })
      } catch (error) {
        this.errorMessage = true
      }
    },
    async getTrailById (id) {
      try {
        await this.$store.dispatch('trails/fetchTrailsById', id)
      } catch (error) {
        this.errorMessage = true
      }
    },
    async displayOnMap (arrayOfCoordinates, trailId) {
      // FAIRE ICI L'APPEL DU STORE POUR ALLER CHERCHER LES LIKES
      try {
        await this.$store.dispatch('like/fetchAllLikesFromTrailsId', trailId)
        await this.$store.dispatch(
          'trails/fetchSegmentFromTrails',
          arrayOfCoordinates
        )
        this.$emit('drawMap', this.segmentsCoordinates)
        if (this.$store.getters['like/getLikeId'](trailId) !== undefined) {
          this.isLiked = true
        } else {
          this.isLiked = false
        }
      } catch (error) {
        this.errorMessage = true
      }
    },
    async likeMethod (trailId) {
      if (this.$store.getters['authentication/isLoggedIn']) {
        const userId = this.$store.getters['authentication/getTokenUserId']
        if (this.isLiked) {
          const likeId = await this.$store.getters['like/getLikeId'](trailId)
          await this.$store.dispatch('like/removeLike', likeId.id)
          await this.$store.dispatch('like/fetchAllLikesFromTrailsId', trailId)
          this.isLiked = false
        } else {
          await this.$store.dispatch('like/addLike', {
            trailId: this.trailSelected.trailId,
            userId: parseInt(userId),
            token: this.$store.state.authentication.token
          })
          this.isLiked = true
          await this.$store.dispatch('like/fetchAllLikesFromTrailsId', trailId)
        }
        await this.$store.dispatch('like/fetchLikedTrails', {
          userId: this.$store.getters['authentication/getTokenUserId']
        })
      } else {
        alert('Vous devez être authentifié pour pouvoir aimer ce sentier')
      }
    }
  },
  computed: {
    parks () {
      return this.$store.state.park.parks
    },
    trailsById () {
      return this.$store.state.trails.trails
    },
    segmentsCoordinates () {
      return this.$store.state.trails.segmentsCoordinates
    },
    segmentsDifficulties () {
      return this.$store.state.trails.segmentsDifficulties
    },
    allTrailLikes () {
      return this.$store.getters['like/getAllTrailLikes'].length
    }
  }
}
</script>

<style lang="scss" scoped></style>
