import { API } from '@/shared/config'
import { parseAxiosErrorToAppError } from '@/shared/errorHelper'
import axios from 'axios'
import requestInterceptor from '@/shared/requestInterceptor'

async function getUsersLike (id) {
  try {
    const response = await requestInterceptor.get(
      `${API}/api/users/${id}/likes`
    )
    return response.data
  } catch (error) {
    throw parseAxiosErrorToAppError(error)
  }
}
async function fetchAllLikesFromTrailsId (id) {
  try {
    const response = await axios.get(`${API}/api/trails/${id}/likes`)
    return response.data
  } catch (error) {
    throw parseAxiosErrorToAppError(error)
  }
}

async function addLikeService (idTrail, idUser) {
  try {
    const response = await requestInterceptor.post(`${API}/api/likes`, {
      userId: idUser,
      trailId: idTrail
    })
    return response.data
  } catch (error) {
    throw parseAxiosErrorToAppError(error)
  }
}

async function removeLike (userId) {
  try {
    const response = await requestInterceptor.delete(
      `${API}/api/likes/${userId}`
    )
    return response
  } catch (error) {
    throw parseAxiosErrorToAppError(error)
  }
}

export const likeService = {
  getUsersLike,
  fetchAllLikesFromTrailsId,
  addLikeService,
  removeLike
}
