<template>
  <div>
    <b-container>
      <b-form @submit.prevent="login">
        <h1>Connexion</h1>
        <label>Courriel</label>
        <b-form-input required v-model="email" type="text" />
        <label>Mot de passe</label>
        <b-form-input required v-model="password" type="password" />
        <b-button variant="primary" type="submit" class="mt-4"
          >Se connecter</b-button
        >
      </b-form>

      <div v-if="authServiceError">{{ authServiceError }}</div>
    </b-container>
    <div>
      <b-container>
        <b-form @submit.prevent="register">
          <h1>Si vous n'avez pas de compte : créer un compte</h1>
          <label>Courriel</label>
          <b-form-input required v-model="registerEmail" type="text" />
          <label>Nom d'utilisateur</label>
          <b-form-input required v-model="registerName" type="text" />

          <label>Mot de passe</label>
          <b-form-input required v-model="registerPassword" type="password" />
          <b-button type="submit" variant="primary" class="mt-4"
            >Créer le compte</b-button
          >
        </b-form>
        <div v-if="registerServError">{{ registerServError }}</div>
        <div v-if="registerServError == '' && registerComplete == true">
          Inscription réussie
        </div>
      </b-container>
    </div>
  </div>
</template>
<script>
export default {
  created () {
    this.$store.commit('authentication/clearError')
  },
  data () {
    return {
      email: '',
      password: '',
      name: '',

      registerEmail: '',
      registerPassword: '',
      registerName: '',

      registerComplete: false
    }
  },
  methods: {
    async login () {
      await this.$store.dispatch('authentication/login', {
        email: this.email,
        password: this.password
      })
      if (!this.authServiceError) {
        this.$router.push({
          name: 'Profile'
        })
      }
    },
    async register () {
      try {
        this.$store.commit('authentication/clearError')
        await this.$store.dispatch('authentication/register', {
          email: this.registerEmail,
          password: this.registerPassword,
          name: this.registerName
        })
        this.registerComplete = true
        if (!this.registerServError) {
          this.$router.push({
            name: 'Profile'
          })
        }
      } catch (error) {
        this.registerComplete = false
      }
    }
  },
  computed: {
    authServiceError () {
      return this.$store.state.authentication.authServiceError
    },
    registerServError () {
      return this.$store.state.authentication.registerServError
    }
  }
}
</script>
